.project-links {
    color: #a0a0a0;
    text-decoration: none;
}

.project-links:hover {
    color: snow;
}

.project-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 250px;
    margin: 20px;
    border-radius: 10px; 
}

.project-container:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2); 
}

.project-image {
    width: 250px;
    height: 250px;
    background-image: url(../Banner/me.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.project-description {
    width: 250px;
    text-align: center;
    background-color: rgb(49, 50, 51);
}



